"use client";
import React, { useState, useEffect } from "react";
import { Button } from "../../../components/ui/button";
import { FiEdit, FiTrash2 } from "react-icons/fi";
import {
  Dialog,
  DialogContent,
  DialogFooter,
  DialogHeader,
  DialogTitle,
} from "@/components/ui/dialog";
import { Input } from "@/components/ui/input";
import axiosInstance from "@/lib/axios";
import {
  Select,
  SelectContent,
  SelectItem,
  SelectTrigger,
  SelectValue,
} from "@/components/ui/select";
import dataURLtoFile from "@/lib/img";

export default function Products() {
  const [products, setProducts] = useState([]);
  const [filteredProducts, setFilteredProducts] = useState([]);
  const [searchTerm, setSearchTerm] = useState("");
  const [form, setForm] = useState({
    name: "",
    description: "",
    price: "",
    images: [],
    brand_id: "",
    mb: "",
    is_activation_from_sales: false,
    is_new: false,
    is_popular: false,
    bonus_amount: "",
  });
  const [editingProduct, setEditingProduct] = useState(null);
  const [modalOpen, setModalOpen] = useState(false);
  const [deleteModalOpen, setDeleteModalOpen] = useState(false);
  const [deletingProductId, setDeletingProductId] = useState(null);
  const [deletingImageIndex, setDeletingImageIndex] = useState(null);

  const [loading, setLoading] = useState(false);
  const [creatingProduct, setCreatingProduct] = useState(false);
  const [updatingProduct, setUpdatingProduct] = useState(false);
  const [deletingProduct, setDeletingProduct] = useState(false);
  const [brands, setBrands] = useState([]);

  useEffect(() => {
    const fetchProducts = async () => {
      setLoading(true);
      try {
        const response = await axiosInstance.get("/products");
        setProducts(response.data);
        setFilteredProducts(response.data);
      } catch (error) {
        console.error("Error fetching products:", error);
      } finally {
        setLoading(false);
      }
    };
    const fetchBrands = async () => {
      setLoading(true);
      try {
        const response = await axiosInstance.get("/brands");
        setBrands(response.data);
      } catch (error) {
        console.error("Error fetching brands:", error);
      } finally {
        setLoading(false);
      }
    };
    fetchBrands();
    fetchProducts();
  }, []);

  const openEditModal = (product) => {
    setEditingProduct(product);
    setForm({
      name: product.name,
      description: product.description,
      price: product.price,
      images: product.images,
      brand_id: product.brand_id,
      mb: product.mb,
      is_activation_from_sales: product.is_activation_from_sales,
      is_new: product.is_new,
      is_popular: product.is_popular,
      bonus_amount: product.bonus_amount,
    });
    setModalOpen(true);
  };

  const handleSubmit = async (e) => {
    e.preventDefault();

    // Проверка наличия изображений в форме
    if (form.images.length === 0) {
      alert("Image is required");
      return;
    }

    setCreatingProduct(true);

    const formData = new FormData();
    // Добавляем остальные данные из формы в FormData
    formData.append("name", form.name);
    formData.append("description", form.description);
    formData.append("price", form.price);
    formData.append("brand_id", form.brand_id);
    formData.append("mb", form.mb);
    formData.append("is_activation_from_sales", form.is_activation_from_sales);
    formData.append("is_new", form.is_new);
    formData.append("is_popular", form.is_popular);
    formData.append("bonus_amount", form.bonus_amount);

    // Добавляем изображения
    form.images.forEach((image) => {
      const imageFile = dataURLtoFile(image, "image.jpg"); // Конвертация изображения в файл
      formData.append("images", imageFile);
    });

    try {
      let response;
      if (editingProduct) {
        response = await axiosInstance.put(
          `/products/${editingProduct._id}`,
          formData,
          { headers: { "Content-Type": "multipart/form-data" } }
        );
      } else {
        response = await axiosInstance.post("/products", formData, {
          headers: { "Content-Type": "multipart/form-data" },
        });
        setProducts([...products, response.data]);
      }

      setModalOpen(false);
      setForm({
        name: "",
        description: "",
        price: "",
        images: [],
        brand_id: "",
        mb: "",
        is_activation_from_sales: false,
        is_new: false,
        is_popular: false,
        bonus_amount: "",
      });
      setEditingProduct(null);
    } catch (error) {
      console.error("Error saving product:", error);
    } finally {
      setCreatingProduct(false);
    }
  };

  const handleDeleteProduct = async () => {
    setDeletingProduct(true);
    try {
      await axiosInstance.delete(`/products/${deletingProductId}`);
      setProducts(
        products.filter((product) => product._id !== deletingProductId)
      );
      setDeleteModalOpen(false);
      setDeletingProductId(null);
    } catch (error) {
      console.error("Error deleting product:", error);
    } finally {
      setDeletingProduct(false);
    }
  };

  const handleDeleteImage = () => {
    setForm({
      ...form,
      images: form.images.filter((_, i) => i !== deletingImageIndex),
    });
    setDeletingImageIndex(null);
  };

  const handleAddImages = (e) => {
    const newImages = Array.from(e.target.files).map((file) =>
      URL.createObjectURL(file)
    );
    setForm({
      ...form,
      images: [...form.images, ...newImages],
    });
  };

  const handleRemoveImage = (index) => {
    setDeletingImageIndex(index);
  };

  useEffect(() => {
    setFilteredProducts(
      products.filter(
        (product) =>
          product.name.toLowerCase().includes(searchTerm.toLowerCase()) ||
          product.description
            .toLowerCase()
            .includes(searchTerm.toLowerCase()) ||
          product.price.toString().includes(searchTerm)
      )
    );
  }, [searchTerm, products]);

  return (
    <div className="p-6 space-y-6">
      <h1 className="text-2xl font-bold mb-4">Admin Panel</h1>

      {/* Search input */}
      <div className="mb-6">
        <Input
          placeholder="Search Products..."
          value={searchTerm}
          onChange={(e) => setSearchTerm(e.target.value)}
          className="w-full"
        />
      </div>

      <Button
        onClick={() => setModalOpen(true)}
        className="mb-6 bg-blue-600 text-white"
        disabled={loading}
      >
        Add New Product
      </Button>

      {loading ? (
        <div className="text-center text-gray-500">Loading products...</div>
      ) : (
        <div className="grid grid-cols-1 sm:grid-cols-2 md:grid-cols-3 lg:grid-cols-4 gap-6">
          {filteredProducts.map((product) => (
            <div
              key={product._id}
              className="border rounded-lg p-4 shadow-md hover:shadow-lg transition-all"
            >
              <div className="mb-4">
                <img
                  src={product.images[0]}
                  alt={product.name}
                  className="w-full h-48 object-contain rounded-lg"
                />
              </div>
              <h2 className="text-xl font-semibold">{product.name}</h2>
              <p className="text-sm text-gray-500">{product.description}</p>
              <p className="text-lg font-bold mt-2">${product.price}</p>

              <div className="mt-4 flex justify-between">
                <Button
                  variant="secondary"
                  size="sm"
                  onClick={() => openEditModal(product)}
                  className="bg-yellow-500 text-white"
                >
                  {updatingProduct ? "Saving..." : <FiEdit />}
                </Button>
                <Button
                  variant="destructive"
                  size="sm"
                  onClick={() => {
                    setDeletingProductId(product._id);
                    setDeleteModalOpen(true);
                  }}
                  className="bg-red-600 text-white"
                  disabled={deletingProduct}
                >
                  {deletingProduct ? "Deleting..." : <FiTrash2 />}
                </Button>
              </div>
            </div>
          ))}
        </div>
      )}

      {/* Product Delete Confirmation Modal */}
      <Dialog open={deleteModalOpen} onOpenChange={setDeleteModalOpen}>
        <DialogContent>
          <DialogHeader>
            <DialogTitle>
              Are you sure you want to delete this product?
            </DialogTitle>
          </DialogHeader>
          <DialogFooter>
            <Button
              variant="secondary"
              onClick={() => setDeleteModalOpen(false)}
              className="mr-2"
            >
              Cancel
            </Button>
            <Button
              variant="destructive"
              onClick={handleDeleteProduct}
              disabled={deletingProduct}
            >
              {deletingProduct ? "Deleting..." : "Delete"}
            </Button>
          </DialogFooter>
        </DialogContent>
      </Dialog>

      {/* Product Edit/Add Modal */}
      <Dialog open={modalOpen} onOpenChange={setModalOpen}>
        <DialogContent>
          <DialogHeader>
            <DialogTitle>
              {editingProduct ? "Edit Product" : "Add New Product"}
            </DialogTitle>
          </DialogHeader>

          <form onSubmit={handleSubmit} className="space-y-4">
            <Input
              name="name"
              value={form.name}
              onChange={(e) => setForm({ ...form, name: e.target.value })}
              placeholder="Product Name"
              required
            />
            <Input
              name="description"
              value={form.description}
              onChange={(e) =>
                setForm({ ...form, description: e.target.value })
              }
              placeholder="Description"
              required
            />
            <Input
              name="price"
              value={form.price}
              onChange={(e) => setForm({ ...form, price: e.target.value })}
              type="number"
              placeholder="Price"
              required
            />
            <Select
              value={form.brand_id}
              onValueChange={(value) => setForm({ ...form, brand_id: value })}
            >
              <SelectTrigger>
                <SelectValue placeholder="Select Brand" />
              </SelectTrigger>
              <SelectContent>
                {brands.map((brand) => (
                  <SelectItem key={brand._id} value={brand._id}>
                    {brand.name}
                  </SelectItem>
                ))}
              </SelectContent>
            </Select>

            {/* Checkbox Fields */}
            <div>
              <label>
                <input
                  type="checkbox"
                  checked={form.is_activation_from_sales}
                  onChange={() =>
                    setForm({
                      ...form,
                      is_activation_from_sales: !form.is_activation_from_sales,
                    })
                  }
                />
                Activation from Sales
              </label>
              <label>
                <input
                  type="checkbox"
                  checked={form.is_new}
                  onChange={() => setForm({ ...form, is_new: !form.is_new })}
                />
                New Product
              </label>
              <label>
                <input
                  type="checkbox"
                  checked={form.is_popular}
                  onChange={() =>
                    setForm({ ...form, is_popular: !form.is_popular })
                  }
                />
                Popular Product
              </label>
            </div>

            {/* Image Upload */}
            <div className="space-y-2">
              {form.images.map((image, index) => (
                <div key={index} className="relative">
                  <img
                    src={image}
                    alt={`Product Image ${index + 1}`}
                    className="w-full h-48 object-contain rounded-md"
                  />
                  <button
                    type="button"
                    className="absolute top-0 right-0 bg-red-500 text-white p-1 rounded-full"
                    onClick={() => handleRemoveImage(index)}
                  >
                    X
                  </button>
                </div>
              ))}
              <input
                type="file"
                multiple
                accept="image/*"
                onChange={handleAddImages}
              />
            </div>

            <DialogFooter>
              <Button
                variant="secondary"
                onClick={() => setModalOpen(false)}
                className="mr-2"
              >
                Cancel
              </Button>
              <Button
                type="submit"
                disabled={creatingProduct}
                className="bg-blue-600"
              >
                {creatingProduct
                  ? "Saving..."
                  : editingProduct
                  ? "Save Changes"
                  : "Add Product"}
              </Button>
            </DialogFooter>
          </form>
        </DialogContent>
      </Dialog>
    </div>
  );
}
